<template lang="pug">
.content
    section.addNewDoc
        h1.title Создать новую версию {{ documents.title }}
        app-timer(@dateUpdate="dateUpd")
        b-form(@submit.prevent.stop="addNewVersion" enctype="multipart/form-data")
            b-row
                b-col

                    b-form-group(
                        label="Название версии:"
                        label-for="version"
                        description="Добавьте версию документа")
                        b-form-input(
                            id="version"
                            v-model="docVersion"
                            required)

                    b-form-group(
                        label="Файл:"
                        label-for="file"
                        description="Добавьте файл документа")
                        b-form-file(
                            id="file"
                            @change="getFile($event)"
                            choose-label="Выберите файл"
                            accept=".pdf"
                            ref="fileInput"
                            required)

                    b-embed(
                        type="embed"
                        :src="previewDoc"
                        v-if="previewDoc"
                        )

                    b-form-group(
                        label="Введите краткое описание версии"
                        )   
                        b-form-textarea(
                            v-model="docDescription"
                            placeholder="Описание.."
                            :rows="3"
                            :max-rows="6"
                            )
            b-button(type="submit") Опубликовать

        b-modal(ref="alertModal" hide-footer) {{ infoAlert }}
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
    data() {
        return {
            date: '',
            docVersion: '',
            docDescription: '',
            file: '',
            infoAlert: '',
            previewDoc: '',
            id: this.$router.currentRoute.params.id,
        };
    },
    computed: {
        ...mapGetters("docsStore", ["documents"]),
    },
    methods: {
        ...mapActions('docsStore', ['addNewDocumentVersion', 'getMyDocumentById']),
        dateUpd(newDate) {
            this.date = newDate;
        },
        getFile(event) {
            const file = event.target.files[0];
            if (!file) return;
            // check size file
            if (file.size / 1024 > 50000) {
                this.showAlert('Загружаемый файл должен быть меньше 50 МБ!');
                return;
            }
            // save to send on server
            this.file = file;
            this.previewDoc = `${URL.createObjectURL(file)}`;
        },
        addNewVersion() {
            const formData = new FormData();
            formData.append('id', this.documents._id);
            formData.append('date', this.date);
            formData.append('file', this.file);
            formData.append('version', this.docVersion);
            formData.append('description', this.docDescription);

            this.addNewDocumentVersion(formData)
                .then(() => {
                    this.showAlert('Документ успешно опубликован!');
                    e.target.reset();
                    this.$refs.fileInput.reset();
                })
                .catch(e => {
                    this.showAlert('Произошла ошибка!');
                });
        },
        showAlert(title) {
            this.infoAlert = title;
            this.$refs.alertModal.show();
        }
    },
    created() {
       this.getMyDocumentById(this.id)
       .catch(e => {
            console.log(e);
            this.$router.push("404");
        });
    },
    components: {
        appTimer: require('../addNewDocPage/innerComponents/timer.vue'),
    }
}
</script>



